body, html {
    height: 100%;
    margin: 0;
}

#banner {
	background-color: #43A5B3;
	width: 100%;
	height: 1vh;
}

#login {
	background-image: linear-gradient(180deg, #43A5B3 10%, #FDBE7C 110%);
	border: 0;
	color: 'white';
	height: 100vh;
	overflow:auto;
}

#survey-background {
	background-image: linear-gradient(180deg, #43A5B3 10%, #FDBE7C 110%);
	border: 0;
	color: 'white';
	height: 100vh;
}


#background {
	background-image: linear-gradient(180deg, #43A5B3 10%, #FDBE7C 110%);
	border: 0;
	color: 'white';
	height: 100%;
	overflow:auto;
}

.ui.image{
	padding: 30px;
}

#title-container {
	padding: 0px;
	margin: 20px;
	text-align: center;
}

.title {
	font-family: "Over the Rainbow";
	color: white; 
	font-size: 70px;
	font-size: 10vw;
}

.subtitle {
	font-family: 'Overlock SC', arial;
	color: white; 
	font-size: 40px;
	font-size: 3vw;
	padding: 8%;
}

/* Add space between login and sign up button */
.ui.basic.inverted.button, .ui.basic.inverted.buttons .button {
	margin: 10px;
}

#survey-container {
	display: flex;
	flex-direction: column;
    align-items: center;
    justify-content: center;
    background-image: linear-gradient(180deg, #43A5B3 10%, #FDBE7C 110%);
	border: 0;
	color: 'white';
	height: 100vh;
}

/* Change color of slider BUGGY! */
.span.MuiSlider-root.MuiSlider-colorPrimary{
	color: #43A5B3;
}

#search-container {
	margin: 30px;
	padding: 10px;
	height: 100vh;
}

.form-container {
	margin: 30px;
	padding: 10px;
	height: 100vh;
}

